.iv-backdrop {
  --iv-backdrop: rgba(0, 0, 0, .85);
  --iv-gap: 16px;
  --iv-radius: 4px;
  --iv-color: #fff;
  --iv-accent: #3b82f6;
  --iv-z: 9999;
  /* Added extracted color variables */
  --iv-btn-bg: #1e293b4d;
  --iv-btn-hover: #334155;
  --iv-stage-bg: #1111114d;
  --iv-overlay: #0009;
  --iv-thumbs-bg: #0f172a4d;
}

.iv-backdrop {
  position: fixed;
  inset: 0;
  background: var(--iv-backdrop);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: var(--iv-z);
  padding: clamp(8px, 2vw, 40px);
  opacity: 0;
  transition: opacity .5s ease;

  &.iv-active {
    opacity: 1;
  }

  &.iv-leave {
    opacity: 0;
  }

  &.iv-active .iv-shell {
    transform: scale(1) translateY(0);
    opacity: 1;
  }

  &.iv-leave .iv-shell {
    transform: scale(.9) translateY(10px);
    opacity: 0;
  }
}

.iv-shell {
  font-family: system-ui, sans-serif;
  position: relative;
  max-width: min(92vw, 1400px);
  max-height: 92vh;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: var(--iv-gap);
  color: var(--iv-color);
  transform: scale(.94) translateY(10px);
  opacity: 0;
  transition: transform .35s cubic-bezier(.4, 0, .2, 1), opacity .35s;
}

.iv-stage {
  cursor: default;
  padding: 3rem;
  flex: 1;
  min-height: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  position: relative;
  background: var(--iv-stage-bg);
  border-radius: var(--iv-radius);

  img {
    transform-origin: center;
    display: block;
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
    user-select: none;
    -webkit-user-drag: none;
    transition: .3s ease;
  }

  .iv-tools {
    opacity: .1;
    transition: 1s ease-out;
  }

  &:hover .iv-tools {
    opacity: 1;
  }

  &.iv-grabbing {
    cursor: grabbing;

    img {
      transition: none;
    }
  }
}

.iv-nav-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: var(--iv-btn-bg);
  border: none;
  color: #fff;
  width: 46px;
  height: 46px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border-radius: 6px;
  backdrop-filter: blur(4px);
  padding: 0;
  line-height: 1;

  &:hover {
    background: var(--iv-btn-hover);
  }

  &-left {
    left: 8px;
  }

  &-right {
    right: 8px;
  }
}

.iv-controls {
  position: absolute;
  top: 8px;
  right: 8px;
  display: flex;
  gap: 6px;

  button {
    background: var(--iv-btn-bg);
    border: none;
    color: #fff;
    width: 34px;
    height: 34px;
    border-radius: 6px;
    font-size: 15px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
  }

  button:hover {
    background: var(--iv-btn-hover);
  }
}

.iv-counter {
  position: absolute;
  bottom: 8px;
  left: 10px;
  font-size: 12px;
  opacity: .85;
  background: var(--iv-overlay);
  padding: 4px 8px;
  border-radius: var(--iv-radius);
}

.iv-thumbs {
  display: flex;
  gap: 6px;
  overflow-x: auto;
  padding: 4px;
  background: var(--iv-thumbs-bg);
  border-radius: var(--iv-radius);

  img {
    height: 54px;
    aspect-ratio: 1/1;
    object-fit: cover;
    border: 2px solid transparent;
    border-radius: 3px;
    cursor: pointer;
    opacity: .6;
  }

  img[data-active="true"] {
    border-color: var(--iv-accent);
    opacity: 1;
  }
}

.iv-zoom-indicator {
  position: absolute;
  bottom: 8px;
  right: 10px;
  font-size: 12px;
  background: var(--iv-overlay);
  padding: 4px 8px;
  border-radius: var(--iv-radius);
}

body.iv-lock {
  overflow: hidden;
}

/* Generic icon styling */
.iv-icon {
  width: 28px;
  height: 28px;
  display: block;
  pointer-events: none;
}

.iv-controls button .iv-icon {
  width: 20px;
  height: 20px;
}